<template>
  <div
    class="_page"
    :class="{
      english: $store.getters.adminData.language == 'english',
      arabic: $store.getters.adminData.language == 'arabic',
    }"
  >
    <!-- <div
      class="overlay-image"
      :style="{
        background:
          'url(\'' + adminData.image1 ||
          ''.replace('http://', 'https://') + '\') fixed center center no-repeat',
        backgroundSize: 'cover',
        'z-index': '-1',
      }"
    ></div> -->
    <div v-if="stage != 'null' && userId != 'null'" class="cards-cont">
      <div class="container">
        <h2 class="page-title">{{ "Chapters" }}</h2>
        <div v-if="loading" class="center">
          <loading color="#fff" />
        </div>
        <br />
        <p class="text-center not-videos" v-if="sections.length < 1 && !loading">
          لا يوجد كورسات بعد
        </p>
        <div
          v-if="!loading && sections.length > 0"
          class="cards row justify-content-center"
        >
          <div v-for="section in sections" :key="section._id" class="col-lg-4">
            <div
              :class="{ not_allowed: !section.allowed && section.price > 0 }"
              @click="() => gotoExams(section.number)"
              class="card"
              :key="section._id"
            >
              <div class="section">
                <h3>{{ section.name }}</h3>
                <!-- <h4>{{ "تكلفة الكورس الشهرية" | translate }}</h4> -->
                <h2 dir="ltr">
                  {{ section.price | number }}
                  <span>{{ "جنيه" | translate }}</span>
                </h2>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!---------------------------------------------------------------------->
    <!---------------------------------------------------------------------->
    <!---------------------------------------------------------------------->
