    <!---------------------------------------------------------------------->
    <div v-if="stage == 'null' || userId == 'null'" class="cards-cont">
      <div class="container">
        <h2 class="page-title">{{ "All chapters" }}</h2>
        <div v-if="loading" class="center">
          <loading color="#fff" />
        </div>
        <br />
        <p
          class="text-center not-videos"
          v-if="Object.keys(sections).length < 1 && !loading"
        >
          لا يوجد كورسات بعد
        </p>
        <div v-if="!loading" dir="rtl">
          <div v-for="(sections, stage) in sections" :key="stage" class="cards">
            <h3 class="text-center w-100 titlee py-4" style="color: #eee">
              {{ stage | stage }}
            </h3>
            <hr />
            <div class="row justify-content-center">
              <div v-for="section in sections" :key="section.number" class="col-lg-4">
                <div @click="() => gotoExams(section.number, stage)" class="card">
                  <div class="section">
                    <h3>{{ section.name }}</h3>
                    <!-- <h4>تكلفة الكورس الشهرية</h4> -->
                    <h2 dir="ltr">{{ section.price || 0 | number }} جنيه</h2>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Loading from "../components/loading.vue";
export default {
  components: {
    Loading,
  },
  created() {
    this.loading = true;
    axios.get("/getSectionsForStudent/" + this.stage + "/" + this.userId).then((res) => {
      this.loading = false;
      if (res.data.err) return alert(res.data.err);
      this.sections = res.data.sections;
    });
  },
  data() {
    return {
      sections: [],
      loading: false,
      notFound: false,
    };
  },
  computed: {
    adminData() {
      return this.$store.getters.adminData;
    },
    stage() {
      return this.$store.getters.stage || "null";
    },
    userId() {
      return this.$store.getters.userId || "null";
    },
  },
  methods: {
    gotoExams(unit, stage) {
      if (stage) this.$router.push("/videos/" + stage + "/" + unit);
      else if (!stage) this.$router.push("/videos/" + unit);
    },
  },
};
</script>

